@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --foreground-rgb: 0, 0, 0;
  --background-start-rgb: 225, 225, 225;
  --background-end-rgb: 225, 225, 225;
}

html {
  font-size: 1rem;
}

body {
  margin: 0px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: var(--font-family);
  font-weight: normal;
  color: rgb(var(--foreground-rgb));
  background: linear-gradient(to bottom,
      transparent,
      rgb(var(--background-end-rgb))) rgb(var(--background-start-rgb));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-app-window {
  min-width:1250px;
  min-height: 700px;
}

.grid-header {
  height: 48px;
  line-height: 48px;

  background: #222628;
  color: #fff;
  border: 1px solid #68686e;
  border-bottom: unset;

  font-weight: bold;
  text-align: center;
}

.ag-theme-alpine {
  margin: 10px;
}

.status-panel {
  height: 32px;
  line-height: 32px;

  background: #222628;
  color: #fff;
  border: 1px solid #68686e;
  border-top: unset;

  font-weight: bold;
  text-align: left;
  padding: 0 8px;
}

.filter-bar {
  background: #222628;
  padding: 5px;
  border: 1px solid #68686e;
  border-bottom: unset;
}

.filter-bar>form {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}

.filter-bar label {
  width: 100%;
  margin-right: 15px;
}

.filter-bar input {
  border: none;
  text-decoration: none;
  border-radius: 2px;
  color: #fff;
  font-size: 13px;
}

.filter-bar input[type=text] {
  width: 100%;
  background: #253232;
  padding: 3px 5px;
}

.filter-bar input[type=button] {
  cursor: pointer;
  width: 50px;
  background-color: #244b5f;
  padding: 3px 10px;
}

.ag-theme-alpine .ag-row {
  border: none;
}

.ag-theme-alpine .ag-cell {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  border-collapse: collapse;
  padding-left: 2px;
  padding-right: 2px;
}

.ag-theme-alpine .ag-header-cell {
  border: none;
  border-right: 1px solid #ccc;
  padding-left: 2px;
  padding-right: 2px;
}

.ag-theme-alpine.ag-popup{
  position: absolute!important;
}

.ag-header-cell-label {
  justify-content: center;
}

.ag-theme-alpine .ag-row-even {
  background-color: #f5f5f5;
}
.ag-theme-alpine .ag-row-odd {
  background-color: #ffffff;
}

.data-grid-cell{
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}